}

:root {
  --main-font: "Montserrat", sans-serif;
  --title-font: "Seymour One", sans-serif;
  --secondary-font: "Roboto", sans-serif;
  --accent-color: #f8d24e;
  --primary-text-color: #fcfcfc;
  --secondary-text-color: #131313;
  --additional-text-color: #ababab;
  --background-color: #84a178;
  --secondary-background-color: #efd478;
  --additional-background-color: #d6936d;
  --button-text-color: #000;
  --button-hoover-focus-color: #0a8817;
  --button-disabled-color: #ababab;
  --modal-background-color: #fcfcfc;
  --modal-form-color: rgba(248, 210, 78, .1);
  --modal-ingredients-bgr-color: #9cbe8f;
  --pagination-color: #d9d9d9;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

html {
