}

.page-header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.menu-button {
  width: 32px;
  height: 32px;
  padding: 10px 6px;
}

.menu-icon {
  z-index: 5;
  width: 20px;
  height: 12px;
  display: block;
  position: relative;
}

.menu-icon:before, .menu-icon:after {
  content: "";
}

.menu-icon:before, .menu-icon:after, .menu-icon > span {
  height: 10%;
  width: 100%;
  background-color: #fff;
  transition: all .3s;
  position: absolute;
  left: 0;
}

.menu-icon > span {
  top: 50%;
  transform: scale(1)translate(0, -50%);
}

.menu-icon:before {
  top: 0;
}

.menu-icon:after {
  bottom: 0;
}

.menu-icon.active span {
  transform: scale(0)translate(0, -50%);
}

.menu-icon.active:before {
  top: 50%;
  transform: rotate(45deg)translate(0, -50%);
}

.menu-icon.active:after {
  bottom: 50%;
  transform: rotate(-45deg)translate(0, 50%);
}

.menu.is-open {
  right: 0;
}

.buy-now-button-tablet {
  display: none;
}

.button-wrapper {
  justify-content: center;
  display: flex;
}

